<template>
	<view class="register">
		<view class="title">请完善个人基本信息</view>
		<p class="smtitle">已完善<b style="margin-left: 12rpx;">0%</b></p>
		<view style="width: 100%;padding: 0 95rpx 45rpx 95rpx;">
			<u-line-progress :striped="true" active-color="#337CFA" :percent="percent" :striped-active="true"
				height="18" :show-percent="false"></u-line-progress>
		</view>
		<u-form :model="form" ref="uForm" :error-type="errorType">
		<view class="formBox">
			<view class="mititle">
				<image style="width: 26rpx;margin-right: 12rpx;" mode="widthFix" src="/static/img/timi.png"></image>
				基本身份信息登记
			</view>
			<view class="mybox">
				<u-upload :max-size="5 * 1024 * 1024" max-count="1" :action="action" :file-list="fileList"
					:header="header" @on-remove="onRemove" @on-success="onSuccess"></u-upload>
				<p style="font-size: 28rpx;font-weight: bold;color: #333333;"><span
						style="color:#FF0000;margin-right: 8rpx;">*</span> 请上传您的人脸照片：</p>
			</view>
				<u-form-item label="真实姓名" prop="universalAntiUserMain27" required label-width='140'>
					<u-input v-model="form.universalAntiUserMain27" input-align="right" placeholder="请输入真实姓名" />
				</u-form-item>
				<u-form-item label="性别" prop="sex" required label-width='140'>
					<view class="yuanbox">
						<u-radio-group v-model="form.sex">
							<u-radio name="1">男</u-radio>
							<u-radio name="2">女</u-radio>
						</u-radio-group>
					</view>
				</u-form-item>
				<u-form-item label="身份证号" prop="idcard" required label-width='140'>
					<u-input v-model="form.idcard" input-align="right" placeholder="请输入身份证号" />
				</u-form-item>
		
			<p style="font-size: 28rpx;color: #333333;margin: 20rpx 0 20rpx -20rpx;"><span
					style="color:#FF0000;margin-right: 6rpx;">*</span> 请上传您的中国第二代居民身份证原件：</p>
			<u-grid :col="2" :border="false">
				<u-grid-item bg-color="transparent">
					<view style="position: relative;">
						<image src="/static/img/head.png" mode="widthFix" style="width: 233rpx;"></image>
						<view class="grid-text" style="text-align: center;margin-top: 21rpx;">身份证正面<p
								style="font-size: 23rpx;color: #B1B1B1;">上传您身份证人像面</p>
						</view>
						<u-upload :max-size="10 * 1024 * 1024" max-count="1" :action="action" :file-list="fileList01"
							:header="header" @on-remove="onRemove02" @on-success="onSuccess02"
							style="position: absolute;top:-25rpx;opacity: 0;z-index: 999;"></u-upload>
					</view>
				</u-grid-item>
				<u-grid-item bg-color="transparent">
					<view style="position: relative;">
						<image src="/static/img/bghui.png" mode="widthFix" style="width: 233rpx;"></image>
						<view class="grid-text" style="text-align: center;margin-top: 21rpx;">身份证反面<p
								style="font-size: 23rpx;color: #B1B1B1;">上传您身份证国徽面</p>
						</view>
						<u-upload :max-size="10 * 1024 * 1024" max-count="1" :action="action" :file-list="fileList02"
							:header="header" @on-remove="onRemove03" @on-success="onSuccess03"
							style="position: absolute;top:-25rpx;opacity: 0;z-index: 999;"></u-upload>
					</view>
				</u-grid-item>
			</u-grid>
		</view>
		<view class="formBox" style="margin-top: 20rpx;">
			<view class="mititle">
				<image style="width: 26rpx;margin-right: 12rpx;" mode="widthFix" src="/static/img/timi.png"></image>
				基本身份信息登记
			</view>
				<u-form-item label="工作单位类型" prop="unitStype" right-icon="arrow-right" required label-width='180'>
					<u-input v-model="form.unitStypetxt" @click="Toselect('unit_stype')" :disabled="true"
						input-align="right" placeholder="请选择" />
				</u-form-item>
				<u-form-item label="单位/场所名称" prop="unitName" required label-width='190'>
					<u-input v-model="form.unitName" input-align="right" placeholder="请输入" />
				</u-form-item>
				<u-form-item label="居住地" prop="place" required label-width='140' right-icon="arrow-right">
					<u-input v-model="form.place" @click="regionshow=true" input-align="right" placeholder="省市区"
						:disabled="true" />
				</u-form-item>
				<u-form-item label="单位地点" prop="placeAddress" required label-width='140'>
					<u-input v-model="form.placeAddress" input-align="right" placeholder="输入详细地址如道路、门牌号、小区、楼栋号、单元室" />
				</u-form-item>
				<u-form-item label="政治面貌" prop="politicalOutlook" right-icon="arrow-right" required label-width='180'>
					<u-input v-model="form.politicalOutlooktxt" @click="Toselect('political_outlook')" :disabled="true"
						input-align="right" placeholder="请选择" />
				</u-form-item>
				<u-form-item label="其他联系电话" prop="otherPhone" label-width='180'>
					<u-input v-model="form.otherPhone" input-align="right" placeholder="请填写注册外其他联系电话、门牌号、小区、楼栋号、单元室" />
				</u-form-item>
				<u-form-item label="文化程度" prop="degree" right-icon="arrow-right" label-width='180'>
					<u-input v-model="form.degreetxt" @click="Toselect('degree')" :disabled="true" input-align="right"
						placeholder="请选择" />
				</u-form-item>
				<u-form-item label="特长" prop="skilled">
					<u-input v-model="form.skilled" />
				</u-form-item>
				<u-form-item label="专业" prop="specializedSubject">
					<u-input v-model="form.specializedSubject" />
				</u-form-item>
				<u-form-item label="爱好" prop="like">
					<u-input v-model="form.like" />
				</u-form-item>
		</view>
		<view class="formBox" style="margin-top: 20rpx;">
			<view class="mititle">
				<image style="width: 26rpx;margin-right: 12rpx;" mode="widthFix" src="/static/img/timi.png"></image>
				志愿者信息
			</view>
				<u-form-item label="是否选择领导职务" prop="isLeadershipPosition" required label-width='250'>
					<view class="yuanbox">
						<u-radio-group v-model="form.isLeadershipPosition">
							<u-radio name="Y">是</u-radio>
							<u-radio name="N">否</u-radio>
						</u-radio-group>
					</view>
				</u-form-item>
				<u-form-item v-if="form.isLeadershipPosition=='Y'" label="请选择领导职务" prop="leadRolerFid"
					right-icon="arrow-right" required label-width='250'>
					<u-input v-model="form.leadRolerFidtxt" @click="Toselect('lead_roler')" :disabled="true"
						input-align="right" placeholder="请选择" />
				</u-form-item>
				<u-form-item v-if="form.isLeadershipPosition=='N'" label="是否创建分队" prop="isCreateUnit" required
					label-width='250'>
					<view class="yuanbox">
						<u-radio-group v-model="form.isCreateUnit">
							<u-radio name="Y">是</u-radio>
							<u-radio name="N">否</u-radio>
						</u-radio-group>
					</view>
				</u-form-item>
				<u-form-item v-if="form.isLeadershipPosition=='N' && form.isCreateUnit=='Y' " label="分队名称"
					prop="orgName" required label-width='190'>
					<u-input v-model="form.orgName" input-align="right" placeholder="请输入" />
				</u-form-item>
				<u-form-item v-if="form.isLeadershipPosition=='N' && form.isCreateUnit=='Y' " label="分队图标"
					prop="orgFile" label-width='140'>
					<view class="yuanbox">
						<u-upload :max-size="10 * 1024 * 1024" max-count="1" :action="action" :file-list="fileList03"
							:header="header" @on-remove="onRemove04" @on-success="onSuccess04"></u-upload>
					</view><!-- <u-input v-model="form.orgFile" input-align="right" :disabled="true"/> -->
				</u-form-item>
				<u-form-item v-if="form.isLeadershipPosition=='N' && form.isCreateUnit=='Y' " label="分队地点"
					prop="orgAddress" required label-width='140'>
					<u-input v-model="form.orgAddress" input-align="right" placeholder="输入详细地址如道路、门牌号、小区、楼栋号、单元室" />
				</u-form-item>
				<u-form-item v-if="form.isLeadershipPosition=='N' && form.isCreateUnit=='Y' " label="分队简介"
					prop="orgIntroduction" label-width='180'>
					<u-input v-model="form.orgIntroduction" input-align="right" placeholder="请填写分队简介" />
				</u-form-item>
				<u-form-item v-if="form.isLeadershipPosition=='N' && form.isCreateUnit=='N'" label="志愿者分队" prop="orgFidtxt"
					required right-icon="arrow-right" label-width='180'>
					<u-input v-model="form.orgFidtxt" @click="bmshow=true" :disabled="true" input-align="right"
						placeholder="请选择" />
				</u-form-item>
				<u-form-item v-if="form.isLeadershipPosition=='N' && form.isCreateUnit=='N'" label="分队身份" prop="orleFid"
					required right-icon="arrow-right" label-width='180'>
					<u-input v-model="form.orleFidtxt" @click="Toselect('volunteer_team_identity')" :disabled="true"
						input-align="right" placeholder="请选择" />
				</u-form-item>
			<u-button @click="submit" type="primary">完成录入</u-button>
		</view>
		</u-form>
		<u-toast ref="uToast"></u-toast>
		<!-- 选择器 -->
		<u-picker mode="region" v-model="regionshow" :area-code='["36", "3604","360402"]'
			@confirm="regioncon"></u-picker>
		<u-select v-model="sushe_show" mode="single-column" :list="susheArr" @confirm="sushe_confirm"
			value-name='dictValue' label-name='dictLabel'></u-select>
		<u-select v-model="bmshow" mode="single-column" :list="bmArr" @confirm="bm_confirm" value-name='deptId'
			label-name='deptName'></u-select>
	</view>
</template>

<script>
	import {
		saveDataFlt,
		getDicts,
		listDept
	} from '../../api/index.js'
	import request from '@/api/request.js'
	export default {
		data() {
			return {
				errorType: ['toast'],
				selectTips: null,
				percent: 0,
				header: {
					Authorization: "Bearer " + uni.getStorageSync("token"),
				},
				regionshow: false,
				bmshow: false,
				bmArr: [],
				susheArr: [],
				sushe_show: false,
				action: request.ROOTHTTP+"/file/upload",
				fileList: [],
				fileList01: [],
				fileList02: [],
				fileList03: [],
				imgList01: [],
				imgList02: [],
				imgList03: [],
				imgList04: [],
				form: {
					isLeadershipPosition: 'Y',
					isCreateUnit: 'N',
					orgFid:null
				},
				rules: {
					universalAntiUserMain27: [{
						required: true,
						message: '请输入真实姓名',
						trigger: ['change', 'blur'],
					}],
					sex: [{
						required: true,
						message: '请选择性别',
						trigger: 'change'
					}],
					idcard: [{
						required: true,
						message: '请输入身份证号',
						trigger: 'change'
					}, {
						validator: (rule, value, callback) => {
							return this.$u.test.idCard(value);
						},
						message: '填写正确的身份证号',
						trigger: ['change', 'blur'],
					}],
					unitStype: [{
						required: true,
						message: '请选择工作类型',
						trigger: 'change'
					}],
					unitName: [{
						required: true,
						message: '请输入单位/场所名称',
						trigger: ['change', 'blur'],
					}],
					place: [{
						required: true,
						message: '请选择居住地',
						trigger: ['change', 'blur'],
					}],
					placeAddress: [{
						required: true,
						message: '请输入单位地址',
						trigger: 'change'
					}],
					politicalOutlook: [{
						required: true,
						message: '请选择政治面貌',
						trigger: ['change', 'blur'],
					}],
					leadRolerFid:[{
						required: true,
						message: '请选择领导职务',
						trigger: ['change', 'blur'],
					}],
					isCreateUnit:[{
						required: true,
						message: '请选择是否创建分队',
						trigger: ['change', 'blur'],
					}],
					orgName:[{
						required: true,
						message: '请输入分队名称',
						trigger: ['change', 'blur'],
					}],
					orgAddress:[{
						required: true,
						message: '请输入分队地点',
						trigger: ['change', 'blur'],
					}],
					orgFidtxt:[{
						required: true,
						message: '请选择志愿分队',
						trigger: ['change', 'blur'],
					}],
					orleFid:[{
						required: true,
						message: '请选择分队身份',
						trigger: ['change', 'blur'],
					}],
				}
			}
		},
		onLoad() {
			listDept().then(res => { //志愿者分队
				if (res.code == 200) {
					this.bmArr = res.data;
				}
			})
		},
		onReady() {
			this.$refs.uForm.setRules(this.rules);
		},
		methods: {
			onRemove(index) {
				this.imgList01.splice(index, 1);
			},
			onSuccess(data, index, lists, name) {
				this.imgList01.push(data.data.url);
			},
			onRemove02(index) {
				this.imgList02.splice(index, 1);
			},
			onSuccess02(data, index, lists, name) {
				this.imgList02.push(data.data.url);
			},
			onRemove03(index) {
				this.imgList03.splice(index, 1);
			},
			onSuccess03(data, index, lists, name) {
				this.imgList03.push(data.data.url);
			},
			onRemove04(index) {
				this.imgList04.splice(index, 1);
			},
			onSuccess04(data, index, lists, name) {
				this.imgList04.push(data.data.url);
			},
			regioncon(e) {
				this.form.place = e.province.label + e.city.label + e.area.label;
			},
			Toselect(no) {
				this.selectTips = no;
				getDicts(this.selectTips).then(res => { //系统是否
					if (res.code == 200) {
						this.susheArr = res.data;
						this.sushe_show = true;
					}
				})
			},
			bm_confirm(e) {
				this.form.orgFidtxt = e[0].label;
				this.form.orgFid = e[0].value;
				console.log("this.form.orgFid",this.form.orgFid);
			},
			sushe_confirm(e) {
				if (this.selectTips == 'unit_stype') {
					this.form.unitStypetxt = e[0].label;
					this.form.unitStype = e[0].value;
				} else if (this.selectTips == 'lead_roler') {
					this.form.leadRolerFidtxt = e[0].label;
					this.form.leadRolerFid = e[0].value;
				} else if (this.selectTips == 'degree') {
					this.form.degreetxt = e[0].label;
					this.form.degree = e[0].value;
				} else if (this.selectTips == 'volunteer_team_identity') {
					this.form.orleFidtxt = e[0].label;
					this.form.orleFid = e[0].value;
				} else if (this.selectTips == 'political_outlook') {
					this.form.politicalOutlooktxt = e[0].label;
					this.form.politicalOutlook = e[0].value;
				}
			},
			submit() {
				if(!this.imgList01[0]){
					this.$refs.uToast.show({
						title: '请上传人脸照片'
					});
				}else if(!this.imgList02[0]){
					this.$refs.uToast.show({
						title: '请上传身份证正面'
					});
				}else if(!this.imgList03[0]){
					this.$refs.uToast.show({
						title: '请上传身份证反面'
					});
				}else{
					this.$refs.uForm.validate(valid => {
						if (valid) {
							this.percent = 100;
							this.form.userFile = this.imgList01[0];
							this.form.idcardFrontFiles = this.imgList02[0];
							this.form.idcardReverseSideFiles = this.imgList03[0];
							this.form.orgFile = this.imgList04[0];
							console.log('验证失败', this.form);
							saveDataFlt(this.form).then(res => {
								if (res.code == 200) {
									this.$refs.uToast.show({
										title: '注册提交成功'
									});
									uni.navigateTo({
										url: '../Ordinary/pt_main?type=' + 2
									})
								}
							});
						} else {
							console.log('验证失败');
						}
					});
				}
			}
		}
	}
</script>

<style scoped>
	.register {
		background-color: #F5F6FA;
	}

	.register .title {
		width: 100%;
		text-align: center;
		font-size: 30rpx;
		font-weight: bold;
		color: #333333;
		padding: 32rpx 0 25rpx 0;
	}

	.yuanbox {
		position: absolute;
		right: 0;
	}

	.yuanbox /deep/ .u-list-item {
		width: 80rpx !important;
		height: 80rpx !important;
		border-radius: 50%;
	}

	.mybox {
		text-align: center;
	}

	.mybox /deep/ .u-upload {
		justify-content: center;
	}

	.formBox {
		padding: 0 30rpx 30rpx 30rpx;
		background-color: #fff;
	}

	.mititle {
		font-size: 30rpx;
		font-weight: bold;
		color: #333333;
		height: 90rpx;
		line-height: 90rpx;
		margin: 0 -30rpx;
		padding: 0 30rpx;
		border-bottom: 1px solid #f1f1f1;
	}

	.smtitle {
		width: 100%;
		text-align: center;
		font-size: 24rpx;
		color: #B1B1B1;
	}

	.smtitle b {
		color: #FF9D1D;
	}
</style>